.container {
  margin: 15px 140px 0 0;
  padding: 0;
  min-height: 19px;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  column-gap: 30px;
}

.link {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #e4e5ea;
}

.item {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  transition: all 0.15s ease-in;
  cursor: pointer;
}

.item:hover {
  opacity: 0.7;
}

@media screen and (max-width: 767px) {
  .container {
    margin: 20px auto 16px;
  }

  .itemHide {
    display: none;
  }

  .list {
    column-gap: 17px;
  }

  .item {
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
  }
}
